import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule, CUSTOM_ELEMENTS_SCHEMA } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';

import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';
import { ListPage } from '../pages/list/list';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { DataProvider } from '../providers/data/data';
import { UtilsProvider } from '../providers/utils/utils';
import { NotificationProvider } from '../providers/notification/notification';
import { C3ServiceProvider } from '../providers/c3-service/c3-service';
import { HttpModule } from '@angular/http';

import { HomePageModule } from '../pages/home/home.module';

@NgModule({
  declarations: [
    MyApp,
    ListPage
  ],
  imports: [
    BrowserModule,
    HttpModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/summary-detail/summary-detail.module#SummaryDetailPageModule', name: 'SummaryDetailPage', segment: 'summary-detail', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/channel-detail/channel-detail.module#ChannelDetailPageModule', name: 'ChannelDetailPage', segment: 'channel-detail', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/login/login.module#LoginPageModule', name: 'LoginPage', segment: 'login', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/summary-card/summary-card.module#SummaryCardPageModule', name: 'SummaryCardPage', segment: 'summary-card', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/graph-bullet/graph-bullet.module#GraphBulletPageModule', name: 'GraphBulletPage', segment: 'graph-bullet', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/graph-bardash/graph-bardash.module#GraphBardashPageModule', name: 'GraphBardashPage', segment: 'graph-bardash', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/channel-card/channel-card.module#ChannelCardPageModule', name: 'ChannelCardPage', segment: 'channel-card', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/about/about.module#AboutPageModule', name: 'AboutPage', segment: 'about', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/graph-donut/graph-donut.module#GraphDonutPageModule', name: 'GraphDonutPage', segment: 'graph-donut', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/notification/notification.module#NotificationPageModule', name: 'NotificationPage', segment: 'notification', priority: 'low', defaultHistory: [] }
  ]
}),
    HomePageModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    DataProvider,
    UtilsProvider,
    NotificationProvider,
    C3ServiceProvider
  ],
  schemas: [ CUSTOM_ELEMENTS_SCHEMA ]
})
export class AppModule {}



